.foto img{
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.user{
    width: 150px;
    margin: 8px;
}

.box{
    display: flex;
    flex-wrap: wrap;
    width: 1000px;
}

.pageNum{
    padding: 10px;
    margin: -2px;
    font-size: 16px;
}

.pageNum:hover{
    font-size: 25px;
    font-weight: 600;
    cursor: pointer;
    color: blueviolet;
}

.butPrev, .butNext{
    outline: none;
    background-color: darkolivegreen;
    margin: 0 5px;
}

.boxPaginator{
    width: 100%;
}

.paginator{
    height: 60px;
    margin: 10px 10px;
}

.selectedPage{
    font-size: 30px;
    cursor: pointer;
    color: black;
    font-weight: 600;
}
